<template>
    <div class="card" @click="openLink" @mouseover="playAudio" @mouseleave="resetAudio">
      <div
        class="card-imagecrd"
        :style="{ backgroundImage: `url(${imagen})` }">
      </div>
      <h3 class="card-title">{{ nombre }}</h3>
      <p class="card-description">{{ descripcion }}</p>
      
      <div class="contenedor-dados"><!-- ⚀ ⚁ ⚂ ⚃ ⚄ ⚅ -->
          <div class="dado dado-3">{{ randomDado() }}</div>
          <div class="dado dado-5">{{ randomDado() }}</div>
      </div>
    </div>

  </template>
  
  <script>
  export default {
    name: "CustomCard",
    props: {
      nombre: String,
      descripcion: String,
      imagen: String,
      url_gh: String,
    },
    data() {
      return {
        audio: new Audio(require('@/assets/multimedia/multimedia/swipe.mp3')),
        isPlaying: false,
      };
    },
    methods: {
      openLink() {
        window.open(this.url_gh, "_blank" );
      },
      playAudio() {
        if (!this.isPlaying) {
          this.audio.play();
          this.isPlaying = true;
        }
      },
      stopAudio() {
        this.audio.pause();
        this.audio.currentTime = 0;
      },
      resetAudio() {
        this.isPlaying = false;
      },
      randomDado() {
        const dados = ['⚀', '⚁', '⚂', '⚃', '⚄', '⚅'];
        return dados[Math.floor(Math.random() * dados.length)];
      }
    },
  };
  </script>
  
  <style>

  .card {
    position: relative;
    width: 320px;
    height: 180px;
    border: 1px solid #000000; /*1a1a1a */
    border-radius: 8px;
    overflow: hidden;
    cursor: pointer;
    box-shadow: 0 2px 5px rgba(0, 0, 0, 0.432);
    transition: transform 0.3s ease;
  }

  .card:hover {
    transform: scale(1.05);
    border: 1px solid #ffffff;/* 3A86FF */
  }

  .card-imagecrd {
    height: 200px;
    background-size: cover;
    background-position: center;
    transition: filter 0.3s ease;
    filter: brightness(0.6);
    opacity: 0.8;
  }

  .card:hover .card-imagecrd {
    filter: brightness(0.3);
    opacity: 1;
  }

  .card:hover .card-description{
    opacity: 1;
  }

  .card-title {
    position: absolute;
    top: 10px;
    left: 10px;
    margin: 0;
    font-size: 1.2rem;
    font-weight: bold;
    color: white;
    text-shadow: 1px 1px 3px rgba(0, 0, 0, 1);
    text-align: justify;
    padding-right: 10px;
  }

  .card-description {
    position: absolute;
    bottom: 10px;
    left: 10px;
    margin: 0;
    font-size: 1rem;
    line-height: 1.4;
    color: white;
    text-shadow: 1px 1px 3px rgb(0, 0, 0);
    max-width: 70%;
    opacity: 0;
    text-align: justify;
  }

  /* D A D O S */

  .card .contenedor-dados .dado-5 {
    position: relative;
    color: rgba(255, 255, 255, 0.7);
    transition: all 0.3s ease;
    z-index: 2;
  }

  .card:hover .contenedor-dados .dado-5 {
    color: rgba(255, 255, 255, 1); /* Enciende los textos */
  }

  .contenedor-dados {
    position: absolute;
    width: 200px;
    height: 150px;
    transform: scale(0.4);
    bottom: -45px;
    right: -63px;
    display: flex;
    gap: 1px;
  }

  .dado {
      font-size: 100px;
      position: absolute;
      transition: transform 0.6s ease-in-out, left 0.6s ease-in-out;
  }

  .dado.dado-5 {
    left: 60px;
    top: 10px;
    z-index: 2;
  }

  .contenedor-dados:hover .dado-5 {
      left: 90px;
  }

  .card:hover .dado-5 {
      left: 90px;
  }


  .dado.dado-3 {
    left: 60px;
    z-index: 1;
    transform-origin: 50% 55%;
    opacity: 0;
    color: white;
    transition: left 0.6s ease-in-out, opacity 1s ease-in-out;
  }

  .card:hover .dado-3 {
    animation: girar_antihorario 1s ease-in-out forwards;
    left: 23px;
    opacity: 1;
    top: 4px;
  }

  .card:not(:hover) .dado-3 {
    animation: girar_horario 1s ease-in-out forwards;
    left: 60px;
    opacity: 0;
    top: 4px;
  }

  /* A N I M A C I O N E S */
  @keyframes girar_antihorario {
      0% {transform: rotate(0deg);}
      100% {transform: rotate(-520deg);}
  }

  @keyframes girar_horario {
      0% {transform: rotate(20deg);}
      100% {transform: rotate(540deg);}
  }

  @media (max-width: 700px) {
