<template>
    <div class="form-container">

      <!-- Mostrar gif de carga mientras isLoading sea true -->
      <div v-if="isLoading" class="loading">
        <div class ="dice-container-loading">
          <div class="spin-dice-loading">⚂</div>
          <div style="transform: translateY(6px);">⚄</div>
        </div>
      </div>

      <FormCard
        v-for="item in data"
        :key="item.id"
        :nombre="item.nombre"
        :descripcion="item.descripcion"
        :fecha_inicio="item.fecha_inicio"
        :fecha_fin="item.fecha_fin"
        :relevancia="item.relevancia"
        :img="item.img"
        :urlInfo="item.url_info"
      />
    </div>
  </template>
  
  <script>
  import FormCard from "@/components/Form.vue";
import { onMounted, ref } from "vue";
import supabase from "C:/Users/amado/Desktop/Portafolios/front_3/welcome/src/supabase.js";
  
  export default {
    name: "FormContainer",
    components: { FormCard },
    setup() {
      const data = ref([]);
      const isLoading = ref(true);  // Estado de carga
  
      const fetchData = async () => {
        // ORDENADOS POR RELEVANCIA
        let { data: rows, error } = await supabase.from("f").select("*").order("relevancia", { ascending: false });

        if (error) {
          console.error("Error fetching data:", error);
        } else {
          console.log("Data fetched from Supabase:", rows); // Verifica que los datos lleguen
          data.value = rows;
        }

        isLoading.value = false;  // Datos cargados, cambiar estado
      };
  
      onMounted(fetchData);
  
      return { data, isLoading };  // Pasar el estado de carga al template
    },
    methods: {
      openWeb() {
        if (this.url_web) {
          window.open(this.url_web, "_blank");
        }
      },
      openUbi() {
        if (this.url_ubicacion) {
          window.open(this.url_ubicacion, "_blank");
        }
      },
    },
  };
  </script>
  
  <style>
  .form-container {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center;

    height: 79%;
    padding-right: 10px;
    padding-left: 10px;
    padding-top: 22px;
    padding-bottom: 22px;
    overflow-y: scroll;
    overflow-x: scroll;
    border-radius: 5px;
    border: 1px #f2ff3a  solid;/*  170020c0 */

    background-color: #3a3a31;/* 3a3a31  f6ff7a63*/
    scrollbar-width: none; 
  }

  @media (max-width: 700px) { /* 480px  */
    .form-container{
      height: 95%;
      border-radius: 0px;
    }
  }
  </style>
  