<template>

    <div class="cardexp"  @mouseover="playAudio" @mouseleave="resetAudio">

      <div class="card-content">
        <h3 class="nombrex" @click="openWeb">{{ nombre }}</h3>
        <p>{{ descripcion }}</p>

        <!-- Lugar y Fecha --><!-- azul: 3957ffa1 || verde: 39ff54a1 || amarillo: fff239a1 || rosa: d139ffa1-->
        <div class="info-container-a" @click="openUbi">
          <!-- Condicional para mostrar el div solo si la ubicación no es null -->
          <div v-if="ubicacion" class="flex-container ubic" style="max-width: 50%;">
            <img class="imgfil" :src="require('@/assets/multimedia/multimedia/pin1.png')" />
            <p>{{ ubicacion }}</p>
          </div>

          <div class="flex-container fechas">
            <img class="imgfil" :src="require('@/assets/multimedia/multimedia/calendario.png')" />
            <p>de: {{ fecha_inicio }}, a: {{ fecha_fin ? fecha_fin : 'actualidad' }}</p>
          </div>
        </div>


        <!-- Responsabilidades y logros -->
        <div class="info-container-b">
          <!-- Sección para mostrar RESPONSABILIDADES -->
          <div v-if="resps.length" style="max-width: 50%;">
            <h4>Responsabilidades:</h4>
            <ul>
              <li class="listitemx" v-for="(resp, index) in resps" :key="index">
                {{ resp.descripcion }}
              </li>
            </ul>
          </div>

          <!-- Sección para mostrar LOGROS -->
          <div v-if="logs.length">
            <h4>Logros:</h4>
            <ul>
              <li class="listitemx logrs" v-for="(log, index) in logs" :key="index">
                {{ log.descripcion }}
              </li>
            </ul>
          </div>
        </div>


      </div>
      <div class="card-image nombrex" @click="openWeb" :style="{ backgroundImage: `url(${img})` }"></div>
    </div>

  </template>
  
  <script>
  export default {
    name: "ExpCard",
    props: {
      id: Number,
      nombre: String,
      descripcion: String,
      ubicacion: String,
      url_ubicacion: String,
      fecha_inicio: String,
      fecha_fin: String,
      relevancia: Number,
      url_web: String,
      img: String,
      resps: Array,
      logs: Array,
    },
    data() {
      return {
        audio: new Audio(require('@/assets/multimedia/multimedia/swipe.mp3')), // Cargar el audio en el data
        isPlaying: false,
      };
    },
    methods: {
      openWeb() {
        if (this.url_web) {
          window.open(this.url_web, "_blank");
        }
      },
      openUbi() {
        if (this.url_ubicacion) {
          window.open(this.url_ubicacion, "_blank");
        }
      },
      playAudio() {
        if (!this.isPlaying) {
          this.audio.play(); // Reproduce el audio solo si no se está reproduciendo
          this.isPlaying = true; // Marca que el audio se ha reproducido
        }
      },
      stopAudio() {
        this.audio.pause(); // Detiene el audio
        this.audio.currentTime = 0; // Reinicia el audio al principio
      },
      resetAudio() {
        this.isPlaying = false; // Resetea la bandera cuando el mouse sale de la tarjeta
      }
    },
  };
  </script>
  
