<template>
    <div class="form-card" @mouseover="playAudio" @mouseleave="resetAudio">
      <div class="form-content">
        <h3 class="nombref" @click="openLink">{{ nombre }}</h3>
        <p>{{ descripcion }}</p>
        <span class="flex-containerfrm" style="margin-bottom: 10px;">
          <img class="imgfil" :src="require('@/assets/multimedia/multimedia/calendario.png')" />
          <p>de: {{ fecha_inicio }}, a: {{ fecha_fin ? fecha_fin : 'actualidad' }}</p>
        </span>
      </div>
      <div
        class="form-image nombref" @click="openLink"
        :style="{ backgroundImage: `url(${img})` }"
      ></div>
    </div>

  </template>
  
  <script>
  export default {
    name: "FormCard",
    props: {
      nombre: String,
      descripcion: String,
      fecha_inicio: String,
      fecha_fin: String,
      relevancia: Number,
      img: String,
      urlInfo: String,
    },
    data() {
      return {
        audio: new Audio(require('@/assets/multimedia/multimedia/swipe.mp3')), // Cargar el audio en el data
        isPlaying: false,
      };
    },
    methods: {
      openLink() {
        window.open(this.urlInfo, "_blank");
      },
      playAudio() {
        if (!this.isPlaying) {
          this.audio.play(); // Reproduce el audio solo si no se está reproduciendo
          this.isPlaying = true; // Marca que el audio se ha reproducido
        }
      },
      stopAudio() {
        this.audio.pause(); // Detiene el audio
        this.audio.currentTime = 0; // Reinicia el audio al principio
      },
      resetAudio() {
        this.isPlaying = false; // Resetea la bandera cuando el mouse sale de la tarjeta
      }
    },
  };
  </script>
  
  <style>
