    color: white;
    background-color: #f6ff7a25; /* f6ff7a63 */
  }

  .form-card:hover {
    transform: scale(1.02);
    border-color: #ffffff;
  }

  .form-content {
    flex: 1; /* El contenido toma el espacio disponible */
    padding-left: 15px;
    padding-right: 15px;
    margin-top: -5px;
    text-align: justify;
    color: white;
  }

  .form-image {
    height: 180px;
    width: 180px;
    margin-top: 15px;
    margin-bottom: 15px;
    margin-right: 15px;
    background-size: cover;
    background-position: center;
    border-radius: 5px;
    opacity: 0.6;
  }

  .form-card:hover .form-image{
    opacity: 0.9;
  }

  .flex-containerfrm {
    display: flex; /* Activa Flexbox */
    align-items: center; /* Alinea los elementos verticalmente */
  }

  .flex-containerfrm img {
    width: 40px;
    height: 40px;
    margin-right: 5px; /* Reduce el margen a la derecha de la imagen */
    margin-left: 0; /* Elimina cualquier margen a la izquierda de la imagen */
    align-self: center; /* Alinea la imagen verticalmente con el texto */
  }

  .nombref{
      transition: ease-in 0.09s;
      cursor: pointer;
    }
    
  .nombref:hover {
    transform: scale(1.02);
  }

  .form-card:hover h3{
    color: #f2ff3a;
