<template>
  <div class="card-container">
    <!-- Mostrar gif de carga mientras isLoading sea true -->
    <div v-if="isLoading" class="loading">
      <div class ="dice-container-loading">
        <div class="spin-dice-loading">⚂</div>
        <div style="transform: translateY(6px);">⚄</div>
      </div>
    </div>

    <Card
      v-for="item in data"
      :key="item.id"
      :nombre="item.nombre"
      :descripcion="item.descripcion"
      :imagen="item.img"
      :url_gh="item.url_gh"
    />
  </div>
</template>

<script>
import Card from "@/components/Card.vue";
import { onMounted, ref } from "vue";
import supabase from "C:/Users/amado/Desktop/Portafolios/front_3/welcome/src/supabase.js";

export default {
  name: "CardContainer",
  components: { Card },
  setup() {
    const data = ref([]);
    const isLoading = ref(true);

    const fetchData = async () => {
      // ORDENADOS POR RELEVANCIA
      let { data: rows, error } = await supabase.from("p").select("*").order("relevancia", { ascending: false });
      if (error) {
        console.error("Error fetching data:", error);
      } else {
        console.log("Data fetched from Supabase:", rows);
        data.value = rows;
      }
      isLoading.value = false; 
    };

    onMounted(fetchData);

    return { data, isLoading };
  },
};
</script>

