<!-- CardEContainer.vue -->
<template>
  <div class="Exp-container">

    <!-- Mostrar gif de carga mientras isLoading sea true -->
    <div v-if="isLoading" class="loading">
      <div class ="dice-container-loading">
        <div class="spin-dice-loading">⚂</div>
        <div style="transform: translateY(6px);">⚄</div>
      </div>
    </div>
    
    <ExpCard
      v-for="item in data"
      :key="item.id"
      :id="item.id"
      :nombre="item.nombre"
      :descripcion="item.descripcion"
      :ubicacion="item.ubicacion"
      :url_ubicacion="item.url_ubicacion"
      :fecha_inicio="item.fecha_inicio"
      :fecha_fin="item.fecha_fin"
      :relevancia="item.relevancia"
      :url_web="item.url_web"
      :img="item.img"
      :resps="item.resps"
      :logs="item.logs"
    />
  </div>
</template>

<script>
import ExpCard from "@/components/Exp.vue";
import supabase from "@/supabase.js";
import { onMounted, ref } from "vue";

export default {
  name: "ExpContainer",
  components: { ExpCard },
  setup() {
    const data = ref([]);
    const isLoading = ref(true);  // Estado de carga

    const fetchData = async () => {
      // Traer datos de la tabla 'e'
      // ORDENADOS POR EL MÁS RECIENTE
      let { data: eData, error: eError } = await supabase.from("e").select("*").order("fecha_inicio", { ascending: false });


      if (eError) {
        console.error("Error fetching 'e':", eError);
        return;
      }

      // Para cada elemento en 'e', traer sus relaciones de 'e_resp' y 'e_logs'
      for (let item of eData) {
        // Traer respuestas relacionadas
        let { data: resps } = await supabase
          .from("e_resp")
          .select("*")
          .eq("id_exp", item.id);

        // Traer logs relacionados
        let { data: logs } = await supabase
          .from("e_logs")
          .select("*")
          .eq("id_exp", item.id);

        // Agregar las relaciones al elemento actual
        item.resps = resps || [];
        item.logs = logs || [];
      }

      console.log("Data fetched with relations:", eData);
      data.value = eData;

      isLoading.value = false;  // Datos cargados, cambiar estado
    };

    onMounted(fetchData);

    return { data, isLoading };  // Pasar el estado de carga al template
  },
};
</script>

