  <script>
  import PerfCard from "@/components/PerfCard.vue";
import supabase from "@/supabase.js";
import { onMounted, ref } from "vue";
  
  export default {
    name: "PerfContainer",
    components: { PerfCard },
    setup() {
      const habConData = ref([]);
      const compsData = ref([]);
      const isLoading1 = ref(true);
      const isLoading2 = ref(true);
      
  
      const fetchData1 = async () => {
        // ORDENADO POR ID
        const { data: habConRows, error: habConError } = await supabase.from("hab_con").select("*").order("id", { ascending: true });
        if (habConError) console.error("Error fetching hab_con data:", habConError);
        else habConData.value = habConRows;

        isLoading1.value = false;
        
        
      };

      const fetchData2 = async () => {
        // ORDENADO POR ID
        const { data: compsRows, error: compsError } = await supabase.from("comps").select("*").order("id", { ascending: true });
        if (compsError) console.error("Error fetching comps data:", compsError);
        else compsData.value = compsRows;
        isLoading2.value = false;
      };
  
      onMounted(fetchData1);
      onMounted(fetchData2);
      
      
      return {
        habConData,
        compsData,
        isLoading1,
        isLoading2
        
      };
    },
    data() {
      return {
        audio: new Audio(require('@/assets/multimedia/multimedia/swipe.mp3')),
        isPlaying: false,
      };
    },
    methods:{
      playAudio() {
        if (!this.isPlaying) {
          this.audio.play();
          this.isPlaying = true;
        }
      },
      stopAudio() {
        this.audio.pause();
        this.audio.currentTime = 0;
      },
      resetAudio() {
        this.isPlaying = false;
      }
    },
  };
  </script>
  
